.App {
  text-align: center;
}

.App-field {
  background: #bbada0;
  border: 7px solid #bbada0;
  border-radius: 10px;
  margin: 0 auto;
  width: 455px;
}

.App-field-row {
  display: inline-flex;
}


.App-field-cell {
  background: #cdc1b4;
  border: 7px solid #bbada0;
  border-radius: 10px;
  height: 100px;
  overflow: hidden;
  width: 100px;
}

.App-field-cell-tile {
  background: #eee4da;
  color: #776e65;
  font-size: 50px;
  font-weight: bold;
  line-height: 100px;
  height: 100px;
  width: 100px;
}

